
import {setTitle} from '@/common/js/common';
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
;

export default{
  data(){
    return{
      isLoading:false,
      sex:[],
      sexlist:["男","女"],
      content:"",
      name:"",
      phone:"",
      choose_item:"男",
      alert_show:false,
      dialog_title:"",
    }
  },
  components:{
    Loading,Group,XInput,Checker,CheckerItem,XButton,Alert
  },
  mounted(){
    setTitle("访客邀请");
    document.getElementById("index_loading").style.display="none";
  },
  methods:{
    itemclick(val){
      this.choose_item=val;
    },
    gocode(){
        this.isLoading=true;
        let self = this;
        let items={
          "data": {
            "name":this.name, //姓名
            "sex":this.choose_item , //0男 1女
            "phone":this.phone, //新手机
          }
        };
        if(service_url){
          this.$http.post(service_url+"/o2o/door/wx/door/invite",items).then((data)=>{
              self.isLoading=false;
            if(data.body.statusCode=="ok"){
              self.$router.replace({path:"code_share",query:{"id":data.body.id}});
            }else{
              self.alert_show=true;
              self.content="访问失败";
              self.dialog_title="注意";
            }
          })
        }else{
          self.isLoading=false;
          self.$router.replace({path:"code_share",query:{"id":1}});
        }

    },
    onShow(){},
    onHide(){}
  }
}



